<template>
  <v-container class="bg-white">
    <v-banner><h2 class="h4"><span class="cblu bbred">All {{alljobs.jobcatid.job_cat_name}} Jobs in {{ city }}</span></h2></v-banner>
    <v-row class="py-0 py-md-5">
      <v-col cols="12" md="3">
        <JobSidebar/>
      </v-col>
      <v-col md="9">
        <v-row>
          <h3 class="bg-light py-2">
            <img :src="(url+alljobs.jobcatid.job_img_url)" width="32" class="img-fluid" alt="accounts">
            {{alljobs.jobcatid.job_cat_name}}
          </h3>
          <v-col cols="12" md="6" v-for="(job,i) in jobslist" :key="i">
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col cols="4">
                    <router-link :to="{name: 'jobdetail', params: { id: job.id }}">
                       <img src="https://content4.jdmagicbox.com/comp/def_content/placement_service_candidates/default-placement-service-candidates-296-250.jpg" :alt=job.job class="rounded-circle img-fluid">
                    </router-link>
                  </v-col>
                  <v-col cols="8">
                    <router-link :to="{name: 'jobdetail', params: { id: job.id }}">
                      <h5 class="link-dark fw-bold mb-0">Job: {{job.job_title}}</h5>
                      <h6 class="cblu fw-bold mb-0">{{job.name}}</h6>
                    </router-link>
                    <div class="fs-6 mb-0">Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                    <div class="fs-6"><v-icon small>mdi-map-marker</v-icon>{{job.city}}, {{job.state}}</div>
                    <div class="fs-6"><span class="fw-bold link-dark fs-6">Min Experience: </span>
                      <span v-if="job.min_exp == 0">Fresher</span>
                      <span v-else>{{ job.min_exp }} Years</span>
                    </div>
                    <div class="fw-bold link-dark fs-6">
                      ₹{{job.salary_from}}
                      <span class="fw-semibold">a Month</span></div>
                    <router-link :to="{name: 'jobdetail', params: { id: job.id }}" class="mt-2 cred fs-6">Read More</router-link>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import JobSidebar from "@/views/jobs/JobSidebar";
import axios from "axios";
export default {
  name: "CatcityJobs",
  props: {
    id: Number,
    job_cat_slug: String,
    city: String
  },
  components: {JobSidebar},
  data() {
    return {
      url:'https://bizlx.itechvision.in',
      alljobs: '',
      jobslist: [],
    }
  },
  mounted() {
    this.getJobcategory();
    this.jobList();
  },
  methods: {
    getJobcategory() {
      axios.get(`jobcategory/${this.job_cat_slug}`)
          .then((resp) => {
            console.log(resp.data);
            this.alljobs = resp.data;
          });
    },
    jobList(){
      axios.get(`jobs/${this.job_cat_slug}/${this.city}`)
          .then((resp) =>{
            console.log(resp.data.joblists);
            this.jobslist = resp.data.joblists;
          })
    }
  }
}
</script>

<style scoped>
@media(max-width: 576px){
.v-card__subtitle, .v-card__text, .v-card__title {
    padding: 8px !important;
}
.v-card__subtitle, .v-card__text {
	line-height: 1.1rem;
}
}
</style>